@import '../../style/sass/mixins';

@include style-reset-a-li;

.products-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    
    .product-card{
        margin-top: 1em;
        margin-bottom: 1em;
        box-shadow: 3px 1px 14px 0px lightgray;
        border-radius: 0.6em;
    
        .product-image-container{
            display: flex;
            justify-content: center;

            img{
                border-radius: 0.4em;
            }
        }
        .product-info{
            padding: 1em;
            text-align: center;
    
            h5{
                font-weight: 700;
            }
            .product-price{
                margin: 0;
                font-weight: 400;
                font-size: 1.2em;
            }
        }

        .cart-button{
            display: flex;
            justify-content: space-evenly;
            margin-top: 1em;
            width: 100%;
            height: 2em;
            border: 1px solid black;
            border-radius: 1em;

            .cart-icon-container{
                padding: 0.2em;
                width: 2em;
                i{
                    font-size: 1.2em;
                }
            }
            .cart-text-container{
                padding: 0.1em;
                p{
                    font-weight: 600;
                    font-size: 1em;
                }
            }
            &:hover{
                cursor: pointer;
                box-shadow: inset #dcdcdc 1px 2px 5px;
            }
        }
    }
}